.content-title {
    background: #ffb969;
    padding: 24px;
}

.content-main {
    font-size: 18px;
    max-width: 700px;
    margin: auto;
}

.headings {
    font-family: "Century Gothic", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.text-big {
    font-size: 5vmax;
}

.text-medium {
    font-size: 32px;
    text-align: center;
}

.myImg {
    height: auto;
    width: 100%;
    max-width: 300px;
    min-width: 100px;
}

.footerImg {
    position: absolute;
    right: 0;
    width: 150px;
    top: -30px;
}

.footerIcons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.test {
    position: relative;
}

.footerIcons > * {
    margin-right: 16px;
}

.fandomIcon svg {
    transition: all 0.2s ease-in;
}

.fandomIcon:hover svg {
    transition: all 0.2s ease-in;
    fill: #3fdede;
}